			<p style="line-height:22pt; margin:0pt 0pt 10pt 21.25pt; orphans:0; text-align:justify; text-indent:-21.25pt; widows:0"><span style="font-family:; font-size:9pt">(2)</span><span style="font:7.0pt 'Times New Roman'">&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span><span style="font-family:; font-size:9pt">1-3</span></p>
			<p
				style="line-height:22pt; margin:0pt 0pt 10pt 21.25pt; orphans:0; text-align:justify; text-indent:-21.25pt; widows:0"><span style="font-family:; font-size:9pt">(3)</span><span style="font:7.0pt 'Times New Roman'">&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span></p>
				<p
					style="line-height:22pt; margin:0pt 0pt 10pt 21.25pt; orphans:0; text-align:justify; text-indent:-21.25pt; widows:0"><span style="font-family:; font-size:9pt">(4)</span><span style="font:7.0pt 'Times New Roman'">&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span><span style="font-family:; font-size:9pt"></span></p>
					<ol type="a" style="margin:0pt; padding-left:0pt">
						<li style="font-family:; font-size:9pt; line-height:22pt; margin:0pt 0pt 10pt 12.14pt; orphans:0; padding-left:9.11pt; text-align:justify; text-indent:0pt; widows:0"><span style="font-family:; font-size:9pt">a.</span></li>
					</ol>
					<p style="line-height:22pt; margin:0pt 0pt 10pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"> </span><span style="font-family:; font-size:9pt"></span></p>
					<ol start="2" type="a" style="margin:0pt; padding-left:0pt">
						<li style="font-family:; font-size:9pt; line-height:22pt; margin:0pt 0pt 10pt 12.91pt; orphans:0; padding-left:8.34pt; text-align:justify; text-indent:0pt; widows:0"><span style="font-family:; font-size:9pt">b.</span></li>
					</ol>
					<p style="line-height:22pt; margin:0pt 0pt 10pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span></p>
					<ol start="3" type="a" style="margin:0pt; padding-left:0pt">
						<li style="font-family:; font-size:9pt; line-height:22pt; margin:0pt 0pt 10pt 11.68pt; orphans:0; padding-left:9.57pt; text-align:justify; text-indent:0pt; widows:0"><span style="font-family:; font-size:9pt">c.</span></li>
					</ol>
					<p style="line-height:22pt; margin:0pt 0pt 10pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span>
