						<li style="font-family:; font-size:9pt; line-height:22pt; margin:0pt 0pt 10pt 12.92pt; orphans:0; padding-left:8.33pt; text-align:justify; text-indent:0pt; widows:0"><span style="font-family:; font-size:9pt">d.</span></li>
					</ol>
					<p style="line-height:22pt; margin:0pt 0pt 10pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span>
						<span
							style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span></p>
					<ol start="5" type="a" style="margin:0pt; padding-left:0pt">
						<li style="font-family:; font-size:9pt; line-height:22pt; margin:0pt 0pt 10pt 12.27pt; orphans:0; padding-left:8.98pt; text-align:justify; text-indent:0pt; widows:0"><span style="font-family:; font-size:9pt">e.</span></li>
					</ol>
					<p style="line-height:22pt; margin:0pt 0pt 10pt; orphans:0; text-align:justify; text-indent:21pt; widows:0"><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span>
						<span
							style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt">,</span><span style="font-family:; font-size:9pt"></span><span style="font-family:; font-size:9pt"></span></p>
			</div>
		</div>
		<div class="reviewRight">
			<h2>{{userName}}{{taskNum}}~</h2>
			<p class="reviewTips"> </p>

			<el-table :data="tableData" border height="400" align='center' header-align='center'>
				<el-table-column label="" type='index' width="55" >
				</el-table-column>
				
				<el-table-column label="" width="100">
					<template scope="scope">
						<a :href="scope.row.fileUrl" :download="scope.row.fileName"></a>
					</template>
				</el-table-column>
				<el-table-column label="" width="98">
					<template scope="scope">
										<el-input v-model.number="scope.row.score1" size="small" :disabled="scope.row.ifReview"></el-input>
									</template>
				</el-table-column>
				<el-table-column label="" width="98">
					<template scope="scope">
										<el-input v-model.number="scope.row.score2" size="small" :disabled="scope.row.ifReview"></el-input>
									</template>
				</el-table-column>
				<el-table-column label="" width="98">
					<template scope="scope">
										<el-input v-model.number="scope.row.score3" size="small" :disabled="scope.row.ifReview"></el-input>
									</template>
				</el-table-column>
				<el-table-column label="" width="98">
					<template scope="scope">
										<el-input v-model.number="scope.row.score4" size="small" :disabled="scope.row.ifReview"></el-input>
									</template>
				</el-table-column>
				<el-table-column label="" width="98">
					<template scope="scope">
										<el-input v-model.number="scope.row.score5" size="small" :disabled="scope.row.ifReview"></el-input>
									</template>
				</el-table-column>
				<el-table-column prop='scoreAll' label="" width="98">
				</el-table-column>
				<el-table-column label="" width="55">
					<template slot-scope="scope">
										<el-button @click="handleClick(scope.$index, scope.row)" type="text" size="small"></el-button>
									</template>
				</el-table-column>
				</el-table>
				<el-pagination background layout="prev, pager, next" :total='pageAll' :current-page.sync='currentPage' @current-change='currentChange'>
				</el-pagination>

<!--<ul>
					<li>x x x x x xx x x </li>
					<li v-for = '(item,index) in reviewScore'>
						{{index}} 
						{{item.reviewLink}}
						<el-input v-model.number="item.score1" size="small" :disabled="ifReview"></el-input>
						<el-input v-model.number="item.score2" size="small" :disabled="ifReview"></el-input>
						<el-input v-model.number="item.score3" size="small" :disabled="ifReview"></el-input>
						<el-input v-model.number="item.score4" size="small" :disabled="ifReview"></el-input>
						<el-input v-model.number="item.score5" size="small" :disabled="ifReview"></el-input>
						{{scoreAll}}
					</li>
				</ul>-->
</div>

</div>
</template>

<script>
	import axios from 'axios'
	import Element from 'element-ui'
	export default {
		inject: ['reload'],
		name: 'review',
		data() {
			return {
				userName: '',
				phoneNum: '',
				taskNum: '',
				judgeNotice: '',
				ifNotice: '',
				notDoneTasks: [],
				reviewScore: [],
				//
				pageSize: 5,
				pageNum: 1,
				pageAll: 10,
				currentPage: 1,
				reviewRulePropotion: {
					ployScoreOne: '',
					ployScoreTwo: '',
					ployScoreThree: '',
					ployScoreFour: '',
					ployScoreFive: ''
				},
			}
		},
		mounted() {
			this.GLOBAL.userId = localStorage.getItem('userId');
			axios.get(this.GLOBAL.urlHead + '/user/userDetail', {
					params: {
						userId: this.GLOBAL.userId
					}
				})
				.then(res => {
					this.userName = res.data.data.user.userRealname;
					this.phoneNum = res.data.data.user.userPhone;
					axios.get(this.GLOBAL.specialUrlWeb + '/user/task/notDoneTaskNum/' + Number(this.phoneNum))
						// + Number(this.phoneNum) 18225039892
						.then(res => {
							console.log(this.phoneNum)
							if (res.data.code == 100001) {
								this.taskNum = res.data.data;
							} else {
								this.taskNum = Number(res.data.data);
							}
						}),
						axios.get(this.GLOBAL.specialUrlWeb + '/user/task/notDoneTasks/' + this.pageNum + '/' + this.pageSize)
						.then(res => {
							this.notDoneTasks = res.data.data.list;
							this.pageAll = (Math.ceil(res.data.data.total/res.data.data.pageSize)*10)
							console.log('this.pageAll',this.pageAll)
							for (var i in res.data.data.list) {
										this.reviewScore.push({
											fileName: res.data.data.list[i].fileName,
											fileUrl: res.data.data.list[i].fileUrl
										})
							}
							console.log(this.reviewScore)
						})
				})
			//
				axios.get(this.GLOBAL.specialUrlMan + '/score/ploy')
				.then(res => {
					//					Object.values(this.reviewRulePropotion)[0] = Object.values(res.data.data)[0]
					this.reviewRulePropotion.ployScoreOne = res.data.data.ployScoreOne;
					this.reviewRulePropotion.ployScoreTwo = res.data.data.ployScoreTwo;
					this.reviewRulePropotion.ployScoreThree = res.data.data.ployScoreThree;
					this.reviewRulePropotion.ployScoreFour = res.data.data.ployScoreFour;
					this.reviewRulePropotion.ployScoreFive = res.data.data.ployScoreFive;
				})
		},
		computed: {
			tableData() {
				this.reviewScore.map((v, i) => {
					v.scoreAll = (parseFloat((v.score1*this.reviewRulePropotion.ployScoreOne + v.score2*this.reviewRulePropotion.ployScoreTwo + v.score3*this.reviewRulePropotion.ployScoreThree + v.score4*this.reviewRulePropotion.ployScoreFour + v.score5*this.reviewRulePropotion.ployScoreFive)))
				});
				return this.reviewScore
			}
		},
		methods: {
			currentChange(index) {
				axios.get(this.GLOBAL.specialUrlWeb + '/user/task/notDoneTasks/' + index + '/' + this.pageSize)
				.then(res => {
						this.notDoneTasks = res.data.data.list;
						console.log('res.data.data.list',res.data.data.list)
						this.pageAll = (Math.ceil(res.data.data.total/res.data.data.pageSize)*10)
						this.reviewScore = []
						for (var i in res.data.data.list) {
										this.reviewScore.push({
											fileName: res.data.data.list[i].fileName,
											fileUrl: res.data.data.list[i].fileUrl
										})
							}
						console.log('this.reviewScore',this.reviewScore[0].fileName)
					})
			},
			handleClick(index, row) {
				axios.post(this.GLOBAL.specialUrlWeb + '/user/submit/score', {
						scoreTeamId: this.notDoneTasks[index].taskTeamId,
						scoreOne: Number(this.reviewScore[index].score1),
						scoreTwo: Number(this.reviewScore[index].score2),
						scoreThree: Number(this.reviewScore[index].score3),
						scoreFour: Number(this.reviewScore[index].score4),
						scoreFive: Number(this.reviewScore[index].score5),
//						scoreJudgeId: this.notDoneTasks[index].task_judge_id
					})
					.then(res => {
						if (res.data.code == 100001) {
							var obj = this.reviewScore[index];
							obj.ifReview = true;
							this.reviewScore.splice(index, 1, {
								obj
							});
							this.$alert('', '', {
								confirmButtonText: '',
								callback: action => {
									this.reload()
								}
							});
						} else {
							this.$alert('', '', {
								confirmButtonText: '',
								callback: action => {}
							});
						}
					})
			},
			backToPer() {
				this.$confirm('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
					center: true
				}).then(() => {
					this.$router.push({
						name: 'personalInfo',
					})
				}).catch(() => {
					//					this.$message({
					////						type: 'info',
					////						message: ''
					//					});
				});
			}
		}
	}
</script>

<style scoped>
	* {
		font-family: "microsoft yahei";
	}
	
	.review {}
	
	.review h2 {
		text-align: center;
		margin-bottom: 20px;
	}
	
	.reviewNotice {
		width: 77%;
		font-size: 14px;
		border: 1px solid #dcdfe6;
		padding: 20px;
		border-radius: 15px;
		color: #909399;
	}
	
	.reviewLeft {
		float: left;
		width: 27%;
		margin-bottom: 30px;
	}
	
	.reviewRight {
		float: left;
		width: 800px;
		margin-top: 20px;
		margin-bottom: 30px;
	}
	
	.reviewRight .reviewTips {
		font-size: 12px;
		float: right;
		margin-bottom: 10px;
	}
</style>


