export default {
	inject:['reload'],
	data(){
		return{
			chooseSchool:''
		}
	},
    mounted(){
        $("#schoolName").focus(function(){
		  var top = $(this).position().top+22;
    	  var left = $(this).position().left;
    	  $("div[class='provinceSchool']").css({top:top,left:left});
    	  $("div[class='provinceSchool']").show();
        });
        var provinceArray = "";
		var provicneSelectStr = "";
		for(var i=0,len=province.length;i<len;i++){
		  provinceArray = province[i];
		  provicneSelectStr = provicneSelectStr + "<option value='"+provinceArray[0]+"'>"+provinceArray[1]+"</option>"
		} 
		$("div[class='proSelect'] select").html(provicneSelectStr);
		//
        var selectPro = $("div[class='proSelect'] select").val();
        
		var schoolUlStr = "";
		var schoolListStr = new String(proSchool[selectPro]);
		var schoolListArray = schoolListStr.split(",");
        var tempSchoolName = "";
        for(var i=0,len=schoolListArray.length;i<len;i++){
		  tempSchoolName = schoolListArray[i];
		  //console.log(tempSchoolName.length);
		  if(tempSchoolName.length>13){
		  	schoolUlStr = schoolUlStr + "<li class='DoubleWidthLi'>"+schoolListArray[i]+"</li>"
		  }else {
		  	schoolUlStr = schoolUlStr + "<li>"+schoolListArray[i]+"</li>"
		  }
        }
        
        $("div[class='schoolList'] ul").html(schoolUlStr);
		//
		$("div[class='proSelect'] select").change(function(){
		//   if("99"!=$(this).val()){
		    $("div[class='proSelect'] span").show();
		    $("div[class='proSelect'] input").hide();
		  	schoolUlStr = "";
            schoolListStr = new String(proSchool[$(this).val()]);
            //
            schoolListArray = schoolListStr.split(",");
            
		    for(var i=0,len=schoolListArray.length;i<len;i++){
		  	  tempSchoolName = schoolListArray[i];
			  if(tempSchoolName.length>13){
			  	schoolUlStr = schoolUlStr + "<li class='DoubleWidthLi'>"+schoolListArray[i]+"</li>"
			  }else {
			  	schoolUlStr = schoolUlStr + "<li>"+schoolListArray[i]+"</li>"
			  }
		    }
            $("div[class='schoolList'] ul").html(schoolUlStr);
		//   }else {
		//     $("div[class='schoolList'] ul").html("");
		//     $("div[class='proSelect'] span").hide();
		//     $("div[class='proSelect'] input").show();
		//   }
        });
        
        $("div[class='schoolList'] ul li").on("mouseover",function(){
          
		});
		//mouseout
		$("div[class='schoolList'] ul li").on("mouseout",function(){
          $(this).css("background-color","");
		});
		//
		$("div[class='schoolList'] ul li").on("click",function(){
		  $("#schoolName").val($(this).html());
		  $("div[class='provinceSchool']").hide();
		});
		//
		$("div[class='button'] button").on("click",function(){
		  var flag = $(this).attr("flag");
		  if("0"==flag){
		    $("div[class='provinceSchool']").hide();
		  }else if("1"==flag){
		    var selectPro = $("div[class='proSelect'] select").val();
		    if("99"==selectPro){
		      $("#schoolName").val($("div[class='proSelect'] input").val());
		    }
		  	$("div[class='provinceSchool']").hide();
		  }
        });
    },
    methods:{
        hhh(){
            
		//
		$("div[class='schoolList'] ul li").on("click",function(){
		  $("#schoolName").val($(this).html());
		  $("div[class='provinceSchool']").hide();
	
		});
		this.chooseSchool = $("#schoolName").val()
		
		},
	},
	watch:{
		chooseSchool:function(cur,old){
			this.$emit('schoolName', cur)
		}
	}
}
</script>

<style scoped>
* {margin:0;padding:0;}
	  body{ padding:20px;font:12px "";background:#FFF;}
ul li{ list-style:none;margin: 0;padding: 0;}
	  #schoolName { width:200px;}
	  .provinceSchool { display:none;padding-right:10px;height:260px;border:1px solid #72B9D7; }
	  /* .provinceSchool .title { width:100%;height:30px;} */
	  /* .provinceSchool .title span { margin-left:10px;font-weight:600;color:#FFF;line-height:30px;} */
	  .provinceSchool .proSelect { height:22px;margin:0 0 5px 15px;}
	  .provinceSchool .proSelect select { width:136px;}
	  .provinceSchool .proSelect input { display:none;}
	  .provinceSchool .schoolList {margin-top: 20px; height:200px;margin-left:15px;overflow-y:auto; border:1px solid #72B9D7;}
	  .provinceSchool .schoolList ul { cursor: pointer;}
	  .provinceSchool .schoolList ul li {cursor: pointer; float:left;width:158px;height:22px;margin-left:6px;padding-left:6px;line-height:22px;cursor:pointer;}
		.provinceSchool .schoolList ul li .DoubleWidthLi { width:328px;}
	  .provinceSchool .schoolList ul li:hover { background:#72B9D7;}
	  .provinceSchool .button { width:100%;height:40px;margin-top:8px;}
	  .provinceSchool .button button { float:right;height:30px;margin-right:20px;padding:4px 10px;;border:none;color:#FFF;font-weight:600;cursor:pointer;}</style>
</style>



