	import choseSchool from './choseSchool.vue'
	import Element from 'element-ui'
	import axios from 'axios'
	import bus from '../../../assets/js/eventBus.js'
	var registerInfo = {}
	export default {
		name: 'registerTea',
		components: {
			choseSchool
		},
		data() {
			//
			var checkTel = (rule, value, callback) => {
				if (!value) {
					return callback(new Error(''));
				}
				setTimeout(() => {
					if (!Number.isInteger(value)) {
						callback(new Error(''));
					} else {
						const mobile = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/
						if (!mobile.test(value)) {
							callback(new Error(''));
						} else {
							callback();
						}
					}
				}, 1000);
			};
			//
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error(''));
				} else if (value !== this.ruleForm.pass) {
					callback(new Error('!'));
				} else {
					callback();
				}
			};
			return {
				labelPosition: 'right',
				ruleForm: {
					schoolClass: [],
					program: [],
					school: ''
				},
				rules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, {
						min: 3,
						max: 5,
						message: ' 3  5 ',
						trigger: 'blur'
					}],
					code: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					userName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					birth: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					sex: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					program: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					pass: [{
						validator: validatePass,
						trigger: 'blur'
					}, {
						min: 6,
						message: '6',
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}, {
						min: 6,
						message: '6',
						trigger: 'blur'
					}],
					tel: [{
						validator: checkTel,
						trigger: 'blur'
					}],
					email: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, {
						type: 'email',
						message: '',
						trigger: ['blur', 'change']
					}]
				}
			};
		},
		mounted() {
			registerInfo = this.$route.params.regisData;
			$("#schoolName").focus(function() {
				var top = $(this).position().top + 22;
				var left = $(this).position().left;
				$("div[class='provinceSchool']").css({
					top: top,
					left: left
				});
				$("div[class='provinceSchool']").show();
			});
			var provinceArray = "";
			var provicneSelectStr = "";
			for (var i = 0, len = province.length; i < len; i++) {
				provinceArray = province[i];
				provicneSelectStr = provicneSelectStr + "<option value='" + provinceArray[0] + "'>" + provinceArray[1] + "</option>"
			}
			$("div[class='proSelect'] select").html(provicneSelectStr);
			//
			var selectPro = $("div[class='proSelect'] select").val();
			var schoolUlStr = "";
			var schoolListStr = new String(proSchool[selectPro]);
			var schoolListArray = schoolListStr.split(",");
			var tempSchoolName = "";
			for (var i = 0, len = schoolListArray.length; i < len; i++) {
				tempSchoolName = schoolListArray[i];
				//console.log(tempSchoolName.length);
				if (tempSchoolName.length > 13) {
					schoolUlStr = schoolUlStr + "<li class='DoubleWidthLi'>" + schoolListArray[i] + "</li>"
				} else {
					schoolUlStr = schoolUlStr + "<li>" + schoolListArray[i] + "</li>"
				}
			}
			$("div[class='schoolList'] ul").html(schoolUlStr);
			//
			$("div[class='proSelect'] select").change(function() {
				//   if("99"!=$(this).val()){
				$("div[class='proSelect'] span").show();
				$("div[class='proSelect'] input").hide();
				schoolUlStr = "";
				schoolListStr = new String(proSchool[$(this).val()]);
				//
				schoolListArray = schoolListStr.split(",");
				for (var i = 0, len = schoolListArray.length; i < len; i++) {
					tempSchoolName = schoolListArray[i];
					if (tempSchoolName.length > 13) {
						schoolUlStr = schoolUlStr + "<li class='DoubleWidthLi'>" + schoolListArray[i] + "</li>"
					} else {
						schoolUlStr = schoolUlStr + "<li>" + schoolListArray[i] + "</li>"
					}
				}
				$("div[class='schoolList'] ul").html(schoolUlStr);
				//   }else {
				//     $("div[class='schoolList'] ul").html("");
				//     $("div[class='proSelect'] span").hide();
				//     $("div[class='proSelect'] input").show();
				//   }
			});
			$("div[class='schoolList'] ul li").on("mouseover", function() {
			});
			//mouseout
			$("div[class='schoolList'] ul li").on("mouseout", function() {
				$(this).css("background-color", "");
			});
			//
			$("div[class='schoolList'] ul li").on("click", function() {
				$("#schoolName").val($(this).html());
				$("div[class='provinceSchool']").hide();
			});
			//
			$("div[class='button'] button").on("click", function() {
				var flag = $(this).attr("flag");
				if ("0" == flag) {
					$("div[class='provinceSchool']").hide();
				} else if ("1" == flag) {
					var selectPro = $("div[class='proSelect'] select").val();
					if ("99" == selectPro) {
						$("#schoolName").val($("div[class='proSelect'] input").val());
					}
					$("div[class='provinceSchool']").hide();
				}
			});
		},
		methods: {
			hhh() {
				//
				$("div[class='schoolList'] ul li").on("click", function() {
					$("#schoolName").val($(this).html());
					$("div[class='provinceSchool']").hide();
				});
				schoolName = $("#schoolName").val()
			},
			getvalue(data) {

			},
			submitForm() {
				var jse = new JSEncrypt() // JSEncrypt
				jse.setPublicKey(publicKey) // 
				var rsaTel = jse.encrypt(String(registerInfo.tel)); // 
				var rsaPsd = jse.encrypt(String(registerInfo.pass)); // 
				var rsaName = jse.encrypt(String(registerInfo.name)); // 
				
				if (this.ruleForm.schoolClass == '') {
					this.ruleForm.schoolClass = 'primarySchool'
				} else if (this.ruleForm.schoolClass == '') {
					this.ruleForm.schoolClass = 'juniorSchool'
				} else {
					this.ruleForm.schoolClass = 'highSchool'
				}

				var interestCourse = '';
				for (var i = 0; i < this.ruleForm.program.length; i++) {
					interestCourse = interestCourse + this.ruleForm.program[i] + ','
				}
				interestCourse = interestCourse.substr(0, interestCourse.length - 1)
				if(this.ruleForm.schoolClass == '' || schoolName == '' || interestCourse == ''){
				this.$alert('', '', {
								confirmButtonText: '',
								callback: action => {
						           
						        }
					});
			}else{
				axios.post(this.GLOBAL.urlHead + '/user/register', {
	//				userPhone: rsaTel,
	//				userPassword: rsaPsd,
	//				userRealname: rsaName,
					userPhone: registerInfo.tel,
					userPassword: registerInfo.pass,
					userRealname: registerInfo.name,
					userSex: registerInfo.sex,
					userEmail: registerInfo.email,
					userNickname: registerInfo.userName,
					userCategory: 'teacher',
					userEduLevel: this.ruleForm.schoolClass,
					userSchool: schoolName,
					userCodeLevel: 'newcomer',
					userInterestCourse: interestCourse,
					code: registerInfo.code,
					userBirthday: registerInfo.birth
				}).then(res => {
	
					if (res.data.code = '100001') {
						localStorage.setItem('userId', res.data.data.userId);
						this.GLOBAL.ifLogin = true;
						localStorage.setItem('ifLogin', this.GLOBAL.ifLogin);
						this.$alert('', '', {
							confirmButtonText: '',
							callback: action => {
								this.$router.replace('/');
								this.$router.go(0);
							}
						});
					}
				})
				}
			}
		}
	}
</script>
